[data-theme=light]{--bg-primary: #FFFFFF;--bg-secondary: #E5E7EB;--text-primary: #333333;--text-secondary: #818181;--accent-primary: #3B82F6;--accent-primary-darken: #2563EB;--accent-secondary: #F97316;--accent-secondary-darken: #EA580C;--border-color: #818181}[data-theme=dark]{--bg-primary: #121212;--bg-secondary: #1E1E1E;--text-primary: #E5E7EB;--text-secondary: #818181;--accent-primary: #3B82F6;--accent-primary-darken: #2563EB;--accent-secondary: #F97316;--accent-secondary-darken: #EA580C;--border-color: #818181}.auth-form{display:flex;align-items:center;justify-content:center;height:calc(100% - 50px)}.form{max-width:750px;margin:0 auto;padding:1rem 4rem;border-radius:5px;background-color:var(--bg-secondary);box-shadow:0 0 10px rgba(0,0,0,.1);display:flex;flex-direction:column;gap:1rem;place-items:center;height:fit-content}.form h6{text-align:center;text-decoration:none}.form_field{display:flex;flex-direction:column;gap:.5rem}.form_field input{border-bottom:1px solid var(--border-color);background-color:var(--bg-primary);color:var(--text-primary);font-size:1rem}.form .form_status{animation:blinkingRed 1s infinite}@keyframes blinkingRed{0%{color:red;transform:scale(1)}50%{color:var(--text-primary);transform:scale(1.1)}100%{color:red;transform:scale(1)}}